.about-container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap:  15%;
}
.about-me{
    margin-left: 40px;
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-bg-varient),
        transparent
    );
    display: grid;
    place-items: center;
}
.about-img{
    
    border-radius: 2rem;
    width: 32rem;
    height: 35rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: all 400ms ease;;
}
.about-img:hover{
    transform: rotate(0);
}
.about-cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}
.about-card{
    background: var(--color-bg-varient);
    border: 1px solid transparent;
    border-radius: 1rem;
    text-align: center;
    transition: var(--transition);
}
.about-card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about-icon{
    color: var(--color-primary);
    font-size: 1.4rem ;
    margin-top: 1rem;
}
.about-card h5{
    font-size: 0.9rem;
}
.about-card small{
    font-size: 0.7rem;
    color:var(--color-light);
}
.about-content p{
    margin: 2rem 0 2rem;
color:var(--color-light);
}

    @media screen and (max-width: 1024px) {
        .about-container{
            grid-template-columns: 1fr;
            gap: 0;
        }
        .about-me{
            width: 50%;
            margin: 2rem auto 4rem;
        }
        .about-content p{
            margin: 1rem 0 1.5rem;
        }
        }

@media screen and (max-width: 600px) {
    .about-content{
        text-align: center;
    }
    .about-cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about-me{
        width: 60%;
        margin: 0 auto 3rem;
    }
    .about-img{
        width: 25rem;
        height: 20rem;
        margin-right: 100px;
    }
    .about-content p{
        margin: 1.5rem 0;
    }
            }